
import instance from "./header";


const request = class  {
    constructor(url,arg) {
        this.url = url;
        this.arg = arg;
    }
    //post请求
    post(){
        return new Promise((resolve,reject)=>{
            instance.post(this.url,this.arg)
                .then(res=>{
                    resolve(res)
                }).catch(err=>{
                    reject(err)
            })
        })
    }
// get请求
    get() {
        return new Promise((reject,resolve)=>{
            instance.get(this.url)
                .then(res=>{
                    resolve(res)
                }).catch(err=>{
                    reject(err)
            })
        }).catch(err=>{})
    }
}

export default request

