import { ElMessage } from "element-plus";

const tips = class  {
    constructor(message = message,type = 'success') {
        this.message = message;
        this.type = type;
    }
    //elmessage提示
    mess_age(){
        ElMessage({
            message: this.message,
            type: this.type,
        })
    }
}

export default  tips
